body{
    background: #f0f0f0; 
}

.profileOnApp, .infoOnApp{ /*App.js*/
    padding: 10px !important; 
}
@media (min-width:992px){
    main{
        padding-left: 0!important; 
    }
}
.inner {
    background: #fff; 
    padding: 20px; 
}
h1{
    font-size: 18px; 
    margin-bottom: 0; 
    font-weight: 700; 
}
h2{
    font-size: 13px;
    margin-top: 10px; 
    color: #777;  
}
h3{
    font-size: 10px; 
    font-weight: 700; 
    margin-bottom: 0; 
}
h4{
    font-size: 8px; 
    font-weight: 700; 
}
section h2{
    padding-bottom: 10px; 
    border-bottom:1px solid #f0f0f0; 
    margin-bottom: 20px; 
}
a{
    -webkit-transition: all 0.5s ease-out; 
    -moz-transition: all 0.5s ease-out;
    -o-transition: all 0.5s ease-out; 
    -ms-transition: all 0.5s ease-out; 
    transition: all 0.5s ease-out 
}
.divider{ /*Profile.js*/
    padding: 10px 20px 0 20px; 
    border-bottom: 1px solid #f0f0f0; 
    margin-bottom: 20px; 
} 
.fa{
    color: #777; 
    margin-right: 5px; 
    top: 4px;  
}
.contact-links li {
    margin-bottom: 15px
}
.profileLinks li a{
    text-decoration: none; 
    color: #000; 
}
.profileName{ /*Profile.js*/
    text-align: center; 
    font-size: 30px; 
}
img{
    margin: auto; 
    display: block;  
}
.profileLinks li a:hover{
    color: #23527c; 
}

.about, .work, .education, .skills{
    margin-bottom: 30px; 
}
.skills-list li{
    font-size: 18px; 
}
.workDates{
    color: #777; 
}

.list-unstyled{
    list-style-type: none; 
}